* {
    margin: 0;
    padding: 0;
    border-radius: 0px;
    box-sizing: border-box;
    font-size: 1.5rem;
}

body {
    background-image: linear-gradient(to right top, #5038de, #4b53e5, #4e69ea, #597dec, #6990eb, #61a1f2, #61b0f6, #69bff8, #52cffe, #41dfff, #46eefa, #5ffbf1);
}

#root {
    text-align: center;
    height: 100vh;
    margin: 0;
    padding: 0;
}


.calculator-title{
    font-size:30px;
    background: #010146;
    width: 400px;
    padding: 10px 10px;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 4px;
    color: #ffffff;
}

.mainCalc{
    margin:0px;
    padding:0px;
    border-radius: 0px;
    box-sizing: border-box;
}

.screen-row input{
width: 400px;
background: rgb(141, 141, 141);
border: 0px;
font-weight: bold;
color: #a6ff16;
-webkit-text-stroke-width: 1px;
-webkit-text-stroke-color: #000000;
padding: 10px;
text-align: right;
}

.footer {
    margin-top: 1rem;
    color: white;
}

a {
    color: white;
}

input[type="button"]{
    width: 100px;
    background: #1b531d;
    border: 1px solid #010146;
    padding: 10px 20px;
    color: rgb(255, 255, 255);
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}
    
input[type="button"]:active {
    background: #ccc;
}